@import "tufte/fonts-og";

html {
  font-size: 13px;
  font-weight: 400;
}

body {
  //width: 87.5%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 12.5%;
  font-family: et-book, Palatino, "Palatino Linotype", "Palatino LT STD",
    "Book Antiqua", Georgia, serif;
  background-color: #fffff8;
  color: #111;
  max-width: 1400px;
  counter-reset: sidenote-counter;
}

/*
@media (prefers-color-scheme:dark) {
    body {
        background-color: #151515;
        color: #ddd
    }
    $contrast-color: #ddd;
    th,td {
    color: #ddd;
    }
  }
*/
h1 {
  font-weight: 400;
  margin-top: 4rem;
  margin-bottom: 1.5rem;
  font-size: 3.2rem;
  line-height: 1;
}

h2 {
  font-style: italic;
  font-weight: 400;
  margin-top: 2.1rem;
  margin-bottom: 1.4rem;
  font-size: 2.2rem;
  line-height: 1;
}

h3 {
  font-style: italic;
  font-weight: 400;
  font-size: 1.7rem;
  margin-top: 2rem;
  margin-bottom: 1.4rem;
  line-height: 1;
}

h4 {
  font-style: italic;
  font-weight: 400;
  font-size: 1.65rem;
  margin-top: 2rem;
  margin-bottom: 1.4rem;
  line-height: 1;
}

h5 {
  font-style: italic;
  font-weight: 400;
  font-size: 1.75rem;
  margin-top: 2rem;
  margin-bottom: 1.4rem;
  line-height: 1;
}

h6 {
  font-style: italic;
  font-weight: 400;
  font-size: 1.45rem;
  margin-top: 2rem;
  margin-bottom: 1.4rem;
  line-height: 1;
}

hr {
  display: block;
  height: 1px;
  width: 55%;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
}

p.subtitle {
  font-style: italic;
  margin-top: 1rem;
  margin-bottom: 1rem;
  font-size: 1.8rem;
  display: block;
  line-height: 1;
}

.numeral {
  font-family: et-book-roman-old-style;
}

.danger {
  color: red;
}

article {
  padding: 0 0 5rem 0;
}

/*
{
    padding-top: 1rem;
    padding-bottom: 1rem
}
*/

dl,
ol,
p,
ul {
  font-size: 1.4rem;
  line-height: 2rem;
}

p {
  margin-top: 1.4rem;
  margin-bottom: 1.4rem;
  padding-right: 0;
  vertical-align: baseline;
}

