*/
li p {
  width: 100%;
}


/* Chapter Epigraphs */
div.epigraph {
  //margin: 3.5636rem 0;
  margin: 1em 0;
  width:55%;
}

div.epigraph > blockquote {
  margin-top: 3rem;
  margin-bottom: 3rem;
}

div.epigraph > blockquote,
div.epigraph > blockquote > p {
  font-style: italic;
}

div.epigraph > blockquote > footer {
  font-style: normal;
  width: 100%;
  padding-left: 2.5%;
  padding-right: 2.5%;
  padding-top: 0;
  margin-top: 0.4455rem;
  //margin-bottom: 3.1182rem;
}

div.epigraph > blockquote > footer > cite {
  font-style: italic;
}
/* end chapter epigraphs styles */

blockquote p,
blockquote footer {
  width: 100%;
  padding-left: 2.5%;
  padding-right: 2.5%;
  margin-top: 0;
}

blockquote footer {
  font-size: 1.1rem;
  line-height: 1.78181818;
  margin-top: 1.78181818rem;
  margin-bottom: 1.78181818rem;
  text-align: right;
}

footer,
p,
table {
  width: 55%;
}

dl,
ol,
ul {
  width: 50%;
  -webkit-padding-start: 5%;
}

dt:not(:first-child),
li:not(:first-child) {
  margin-top: 0.25rem;
}

figure {
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  max-width: 55%;
  -webkit-margin-start: 0;
  -webkit-margin-end: 0;
  margin: 0 0 3em 0;
}

figcaption {
  float: right;
  clear: right;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 1.1rem;
  line-height: 1.6;
  vertical-align: baseline;
  position: relative;
  max-width: 40%;
}

figure.fullwidth figcaption {
  margin-right: 24%;
}

a:link,
a:visited {
  //color: $contrast-color;
  color: inherit;
  transition: all 0.5s;
}

.no-tufte-underline:link {
  background: unset;
  text-shadow: unset;
}

.hover-tufte-underline:hover,
.tufte-underline,
a:link {
  text-decoration: none;
  background: -webkit-linear-gradient(#fffff8, #fffff8),
    -webkit-linear-gradient(#fffff8, #fffff8),
    -webkit-linear-gradient(currentColor, currentColor);
  background: linear-gradient(#fffff8, #fffff8),
    linear-gradient(#fffff8, #fffff8),
    linear-gradient(currentColor, currentColor);
  -webkit-background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
  -moz-background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
  background-size: 0.05em 1px, 0.05em 1px, 1px 1px;
  background-repeat: no-repeat, no-repeat, repeat-x;
  text-shadow: 0.03em 0 #fffff8, -0.03em 0 #fffff8, 0 0.03em #fffff8,
    0 -0.03em #fffff8, 0.06em 0 #fffff8, -0.06em 0 #fffff8, 0.09em 0 #fffff8,
    -0.09em 0 #fffff8, 0.12em 0 #fffff8, -0.12em 0 #fffff8, 0.15em 0 #fffff8,
    -0.15em 0 #fffff8;
  background-position: 0 93%, 100% 93%, 0 93%;
}
a:link:hover {
  color: #9a9671;
}

@media screen and (-webkit-min-device-pixel-ratio: 0) {
  .hover-tufte-underline:hover,
  .tufte-underline,
  a:link {
    background-position-y: 87%, 87%, 87%;
  }
}
/*
@media (prefers-color-scheme:dark) {

    .hover-tufte-underline:hover,
    .tufte-underline,
    a:link {
        text-shadow: .03em 0 #151515, -.03em 0 #151515, 0 .03em #151515, 0 -.03em #151515, .06em 0 #151515, -.06em 0 #151515, .09em 0 #151515, -.09em 0 #151515, .12em 0 #151515, -.12em 0 #151515, .15em 0 #151515, -.15em 0 #151515
    }
}
*/
a:link::-moz-selection,
a:link::selection {
  text-shadow: 0.03em 0 #b4d5fe, -0.03em 0 #b4d5fe, 0 0.03em #b4d5fe,
    0 -0.03em #b4d5fe, 0.06em 0 #b4d5fe, -0.06em 0 #b4d5fe, 0.09em 0 #b4d5fe,
    -0.09em 0 #b4d5fe, 0.12em 0 #b4d5fe, -0.12em 0 #b4d5fe, 0.15em 0 #b4d5fe,
    -0.15em 0 #b4d5fe;
  background: #b4d5fe;
}

img {
  max-width: 100%;
}

.marginnote,
.sidenote {
  float: right;
  clear: right;
  margin-right: -60%;
  width: 50%;
  margin-top: 0.3rem;
  margin-bottom: 0;
  font-size: 1.1rem;
  line-height: 1.3;
  vertical-align: baseline;
  position: relative;
}

.sidenote-number {
  counter-increment: sidenote-counter;
}

.sidenote-number:after,
.sidenote:before {
  font-family: et-book-roman-old-style;
  position: relative;
  vertical-align: baseline;
}

.sidenote-number:after {
  content: counter(sidenote-counter);
  font-size: 1rem;
  top: -0.5rem;
  left: 0.1rem;
}

.sidenote:before {
  content: counter(sidenote-counter) " ";
  font-size: 1rem;
  top: -0.5rem;
}

blockquote .marginnote,
blockquote .sidenote {
  margin-right: -82%;
  min-width: 59%;
  text-align: left;
}

div.fullwidth,
table.fullwidth {
  width: 100%;
}

div.table-wrapper {
  overflow-x: auto;
  font-family: "Trebuchet MS", "Gill Sans", "Gill Sans MT", sans-serif;
}

.sans {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, sans-serif;
  letter-spacing: 0.03em;
}

code,
pre > code {
  font-family: Consolas, "Liberation Mono", Menlo, Courier, monospace;
  font-size: 1.2rem;
  line-height: 1.42;
  -webkit-text-size-adjust: 100%;
}

.sans > code {
  font-size: 1.2rem;
}

h1 > code,
h2 > code,
h3 > code,
h4 > code,
h5 > code,
h6 > code {
  font-size: 0.8em;
}

.marginnote > code,
.sidenote > code {
  font-size: 1rem;
}

pre > code {
  font-size: 1.2rem;
  //width: 52.5%;
  margin-left: 2.5%;
  overflow-x: auto;
  display: block;
}

pre.fullwidth > code {
  width: 90%;
}

.fullwidth {
  max-width: 90%;
  clear: both;
}

span.newthought {
  font-variant: small-caps;
  font-size: 1.2em;
}

input.margin-toggle {
  display: none;
}

label.sidenote-number {
  display: inline-block;
  max-height: 2rem;
}

label.margin-toggle:not(.sidenote-number) {
  display: none;
}

.iframe-wrapper {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 25px;
  height: 0;
}

.iframe-wrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media (max-width: 760px) {
  body {
    width: 84%;
    padding-left: 8%;
    padding-right: 8%;
  }

  hr,
  footer,
  p,
  table {
    width: 100%;
  }

  pre > code {
    width: 97%;
  }

  dl,
  ol,
  ul {
    width: 90%;
  }

  figure {
    max-width: 90%;
  }

  figcaption,
  figure.fullwidth figcaption {
    margin-right: 0;
    max-width: none;
  }

  blockquote {
    margin-left: 1.5em;
    margin-right: 0;
  }

  blockquote footer,
  blockquote p {
    width: 100%;
  }

  label.margin-toggle:not(.sidenote-number) {
    display: inline;
  }

  .marginnote,
  .sidenote {
    display: none;
  }

  .margin-toggle:checked + .marginnote,
  .margin-toggle:checked + .sidenote {
    display: block;
    float: left;
    left: 1rem;
    clear: both;
    width: 95%;
    margin: 1rem 2.5%;
    vertical-align: baseline;
    position: relative;
  }

  label {
    cursor: pointer;
  }

  div.table-wrapper,
  table {
    width: 85%;
  }

  img {
    width: 100%;
  }
}

// Start of Nav and Footer styling area

header > nav.group,
body footer {
  width: 95%;
  padding-top: 2rem;
}

nav.group a.active,
nav a.active {
  color: black !important;
}

nav.group a.active:before {
  content: "\0003c\000a0";
}

// escaped unicode for the carats and then a space on active menu links
nav.group a.active:after {
  content: "\000a0\0003e";
}

header > nav {
  padding-top: 1.5rem;
}

header > nav a,
div#navbar a {
  font-size: 1.5rem;
  font-family: $sans-font;
  font-weight: 520;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  color: $contrast-color;
  text-decoration: none;
  display: inline-block;
  // float: left;
  margin-top: 0;
  margin-bottom: 0;
  padding-right: 1rem;
  padding-left: 1rem;
  //margin-left: 1rem;
  vertical-align: baseline;
}

#navbar a,
nav a {
  background: none;
}

header > nav a img {
  height: 5rem;
  position: relative;
  max-width: 100%;
  top: -1.5rem;
}

ul.footer-links,
.credits {
  list-style: none;
  text-align: center;
  margin: 0 auto;
}

ul.footer-links li {
  display: inline;
  padding: 0.5rem 0.25rem;
}

.credits {
  padding: 1rem 0rem;
}

//change font color for credit links in footer

.credits {
  font-family: $sans-font;

  & a {
    color: $contrast-color;
  }
}

// End of Nav and Footer styling area
article ul li::marker {
  content: "- ";
  position: absolute;
  margin-left: -20px;
}

/*-- Table styling section - For Tufte-Jekyll, booktabs style is default for Markdown tables  --*/

@media screen and (min-width: 760px) {
  .table-responsive {
    width: 55%;
  }
}

.table-hover {
  width: 100%;
  margin-left: 0;
}

table,
table.booktabs {
  //margin: 0 auto;
  border-spacing: 0px;
  border-top: 2px solid $border-color;
  border-bottom: 2px solid $border-color;
}

.booktabs th,
th {
  border-bottom: 1px solid $border-color;
  padding: 0.65ex 0.5em 0.4ex 0.5em;
  font-weight: normal;
  //text-align: center;
}

th,
td {
  font-size: 1.2rem;
  line-height: 1.71428571;
}

.booktabs th.cmid,
th {
  border-bottom: 1px solid lighten($border-color, 25%);
}

.booktabs th.nocmid {
  border-bottom: none;
}

.booktabs tbody tr:first-child td,
tr:first-child td {
  padding-top: 0.65ex;
}

/* add space between thead row and tbody */

.booktabs td,
td {
  padding-left: 0.5em;
  padding-right: 0.5em;
  text-align: left;
}

.booktabs caption,
caption {
  font-size: 90%;
  text-align: left;
  width: auto;
  margin-left: auto;
  margin-right: auto;
  margin-top: 1ex;
  caption-side: top;
}

.booktabs tbody tr,
.booktabs thead tr th.l,
.booktabs td.l {
  text-align: left !important;
}

.booktabs tbody tr,
.booktabs thead tr th.c,
.booktabs td.c {
  text-align: center !important;
}

.booktabs tbody tr,
.booktabs thead tr th.r,
.booktabs td.r {
  text-align: right !important;
}

.table-caption {
  float: right;
  clear: right;
  margin-right: -60%;
  width: 50%;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 1rem;
  line-height: 1.96;
}

td a {
  text-shadow: none !important;
  color: $contrast-color !important;
  background: none !important;
}
td a:hover {
  color: #9a9671 !important;
}
/* -- End of Table Styling section --*/
@import "../node_modules/bootstrap/scss/tufte+bs.scss";

.endmark {
  margin-bottom: 1.2em;
}

.endmark::after {
  font-size: 1.5em;
  margin-left: 0.1rem;
  @extend .align-top;
  font-family: "Autography";

  height: 1em;
  content: "sk";
}

#top-btn {
  transition: opacity 0.5s ease 0s;
  opacity: 0%;
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 99;
}

@import "tufte/syntax-highlighting";
